.header {
    display: inline-block;
    height: 480px;
    position: relative;
}

.header svg {
    fill: #dddddd;
}

.cycle-tyre-1, .cycle-tyre-2, .cycle-rest {
    left: 0;
    position: absolute;
    top: 48px;
    width: 25%;
}

.cycle-tyre-1.animate, .cycle-tyre-2.animate {
    -webkit-animation-name: rotate;
            animation-name: rotate;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 3.5;
            animation-iteration-count: 3.5;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.cycle-tyre-1.animate, .cycle-tyre-2.animate, .cycle-rest.animate {
    margin-left: 75%;
    -webkit-transition: margin-left 4s linear;
    -o-transition: margin-left 4s linear;
    transition: margin-left 4s linear;
}

.cycle-tyre-1 {
    -webkit-transform-origin: 81% 56%;
        -ms-transform-origin: 81% 56%;
            transform-origin: 81% 56%;
}

.cycle-tyre-2 {
    -webkit-transform-origin: 17% 56%;
        -ms-transform-origin: 17% 56%;
            transform-origin: 17% 56%;
}

.cycle-handle.animate {
    -webkit-animation-name: move;
            animation-name: move;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 3.5;
            animation-iteration-count: 3.5;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform-origin: 70%;
        -ms-transform-origin: 70%;
            transform-origin: 70%;
}

.cycle-path {
    left: 0;
    position: absolute;
    top: 40px;
}

.cycle-path svg {
    width: 25%;
}

.cycle-tyre-shadow {
    fill: #3f3f3f;
}

.header-title {
    margin-top: 320px;
}

@media screen and (max-width: 1000px) {
    .header-title {
        margin-top: 284px;
    }
}

@media screen and (max-width: 600px) {
    .header-title {
        margin-top: 252px;
    }

    .cycle-tyre-1, .cycle-tyre-2, .cycle-rest {
        top: 72px;
    }

    .cycle-path {
        top: 64px;
    }

    .cycle-path svg {
        width: 33.33%;
    }

    .cycle-path svg:last-child {
        display: none;
    }

    .cycle-tyre-1, .cycle-tyre-2, .cycle-rest {
        width: 33.33%;
    }

    .cycle-tyre-1.animate, .cycle-tyre-2.animate, .cycle-rest.animate {
        margin-left: 66.67%;
        -webkit-transition: margin-left 3s linear;
        -o-transition: margin-left 3s linear;
        transition: margin-left 3s linear;
    }

    .cycle-handle.animate, .cycle-tyre-1.animate, .cycle-tyre-2.animate {
        -webkit-animation-iteration-count: 2.75;
                animation-iteration-count: 2.75;
    }
}

@-webkit-keyframes rotate {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes rotate {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@-webkit-keyframes move {
    from {-webkit-transform: rotateY(-45deg);transform: rotateY(-45deg);}
    to {-webkit-transform: rotateY(45deg);transform: rotateY(45deg);}
}

@keyframes move {
    from {-webkit-transform: rotateY(-45deg);transform: rotateY(-45deg);}
    to {-webkit-transform: rotateY(45deg);transform: rotateY(45deg);}
}



